.product_item_thumbnail {
  width: 100%;

  img {
    width: 400px;
    height: 400px;
    border-radius: 6px 6px 0 0;
  }
}

.product-box-cava {
  background-color: #fff;
  padding: 5rem;
  margin: 0 auto;
  width: 100%;
}

.product-box-preview {
  display: flex;
  flex-wrap: wrap;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #f4f5fc;
  max-width: 403px;
  padding: 1px;
  margin: 0 auto;

  &__author{
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: center;
  }
  
  &__avatar{
    width: 7rem !important;
    height: 7rem !important;
    margin: 1rem 0;
    box-sizing: border-box;
    border: 4px solid #fff;
    border-radius: 50% !important;
  }
  
  &__display-name{
    font-size: 1.5rem;
    font-weight: 300;
    margin-top: 0;  
    margin-bottom: .7rem;
    font-weight: 500;
  }
  
  &__email{
    font-size: 1rem;
    display: block;
  }
}

.product_item_details {

  &__meta {
    margin-bottom: .2rem;
  }
}

.wd-product {

  &__content {
    width: 100%;
    padding: 15px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}